.expr-layout {
  margin-top: 5em;
  display: flex;
  justify-content: center;
}
.expr-grid {
  display: flex;
  flex-direction: row;
  margin-top: 5em;
  justify-content: center;
  align-items: center;
  align-items: center;
  width: 100%;
  border-radius: 10px;
}
.expr-grid h3 {
  color: white;
  font-family: Ubuntu;
  font-size: xx-large;
  font-weight: bolder;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 2em;
  padding-left: 1em;
}

.expr-content {
  color: white;
  font-family: Ubuntu;
  font-size: large;
  font-weight: 700;
  width: 80%;
  margin-left: 50px;
  padding: 1em;
  margin: auto;
}
