#nav1 {
  background-color: black; /*whichever you want*/
  opacity: 0.9;
  filter: (opacity=0.01);
}
.nav-layout {
  display: flex;
  height: auto;
  width: auto;
  align-items: center;
}
.nav-layout a:hover {
  color: white;
  text-decoration: none;
}
.nav-anchor {
  display: flex;
  padding-top: 1em;
  font-family: Ubuntu;
  color: gray;
  font-size: large;
  font-weight: bold;
  padding-left: 1em;
}
.nav-brand {
  display: flex;
  padding-top: 15px;
  font-family: ubuntu;
  color: grey;
  font-size: 2em;
}
.nav-brand a {
  color: grey;
  text-decoration: none;
}

.nav-brand a:hover {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.logo {
  margin-left: 15px;
  width: 35px;
  height: 35px;
  border: 3px solid gray;
  background: white;
}
