.p-layout {
  display: flex;
  justify-content: center;
  width: 100%;
}
.p {
  padding-top: 10em;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}
.p-p-style {
  display: flex;
  align-items: center;
  width: 290px;
  height: 290px;
  border-radius: 50%;
  margin: auto;
}
.p h1 {
  padding-top: 1em;
  font-family: "Ubuntu";
  font-size: 40px;
  font-weight: bold;
  color: white;
}
.p p {
  font-family: Ubuntu;
  font-size: 16px;
  font-weight: 500;
  color: white;
}
.intro {
  left: 0;
  margin-top: 5em;
  top: 20px;
  width: 90%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
}
.social-links {
  padding-bottom: 1em;
  padding-top: 0.5em;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 30%;
  margin: auto;
}
.social-links i {
  color: white;
  font-size: 3em;
  padding: 30px;
}
