.about-layout {
  display: flex;
  margin-top: 10em;
}
.about {
  display: flex;
  margin: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 75%;
  background: black;
  border-radius: 8px;
  padding-bottom: 1em;
}
.about h2 {
  font-family: Ubuntu;
  font-weight: bolder;
  display: flex;
  color: white;
  padding-bottom: 2em;
}
.about p {
  font-family: Ubuntu;
  font-weight: lighter;
  font-size: larger;
  text-align: justify;
  color: white;
}
.about-grid {
  padding-top: 5em;
  margin-top: 5em;
  padding-bottom: 10em;
  top: 10px;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
}
