.contact-layout {
  display: flex;
  background: black;
  padding-top: 5em;
  padding-bottom: 5em;
  margin-top: 8em;
  position: relative;
  justify-content: center;
}
.contact-grid {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  width: 40%;
}
.contact-grid h2 {
  color: white;
  font-size: medium;
  font-weight: 10px;
  font-family: Ubuntu;
  text-align: center;
}
.social-links {
  margin-left: auto;
  padding-bottom: 1em;
  padding-top: 0.5em;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  margin: auto;
}
.social-links i {
  color: white;
  font-size: 2em;
  padding: 10px;
}
